<template>
  <div v-if="cart.length">
    <h1>Carrito</h1>
    <v-data-table
        :headers="fields"
        :items="cart"
        class="elevation-1"
    >
      <template v-slot:item.actions="{ item }">

        <v-icon
            small
            @click.stop="removeProductFromCart(item)"
        >
          mdi-delete
        </v-icon>
      </template>

    </v-data-table>
    <v-alert
             dense
             outlined
             type="success"
    >
     <strong>Costo total {{totalCost}}</strong>
    </v-alert>
  </div>
  <v-alert v-else
           dense
           outlined
           type="error"
  >
    No hay <strong>Productos</strong> en el carrito
  </v-alert>


</template>

<script>

import {mapMutations, mapGetters, mapState} from "vuex";

export default {
  name: "Cart",
  data() {
    return {
      fields: [
        {
          text: 'Productos agregados',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        {text: 'Cantidad',  sortable: false, value: 'qty'},
        {text: 'Precio',  sortable: false, value: 'price'},
        {text: 'Acciones',  sortable: false, value: 'actions'},
      ],

      //    ['name', 'qty','price','actions']
    }
  },
  computed: {
    ...mapState('cart', ['cart']),
    ...mapGetters('cart', ['totalCost']),
  },
  methods: {
    ...mapMutations('cart', ['removeProductFromCart'])
  }
}
</script>

